<!-- 手机Loading -->
<template>
  <div class="loading-wrapper-phone">
    <div class="toast toast-loading">
      <img class="closeModal" @click="closeModal" :src="close_icon" alt="关闭按钮">
      <img  class="market" :src="img_src" alt="商城暂未开通">
      <img :src="qrcode" alt="qrcode" class="qrcode">
      <div class="modalText">
        <span>大鹏官方投诉QQ号</span>
        <span>3344832782</span>
        <a href="http://qm.qq.com/cgi-bin/qm/qr?k=doGtX9ApU6SXL3bEqmf0ZRMwKQ-g4KBc">添加好友</a>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    props: [
      'title'
    ],
    data() {
      return {
        img_src: 'https://image.dapengjiaoyu.com/2018/08/jl1p6sir.png',
        close_icon: 'https://image.dapengjiaoyu.com/2018/08/jl1p7n9e.png',
        qrcode: 'https://image.dapengjiaoyu.com/2018/08/jl1p867b.png'
      }
    },
    methods: {
      closeModal(){
        this.$emit('closeMarketModal',0);
      }
    }
  }
</script>
<style lang="less" scoped>
  .loading-wrapper-phone {
    position: fixed;
    z-index: 1000;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, .5);
    .toast {
      left:50%;
      top: 50%;
      transition-duration: .2s;
      z-index: 100;
      position: fixed;
      border-radius: 5px;
      color: #fff;
      transform: translate(-50%,-50%);
      &>.closeModal{
        position: absolute;
        top: 130px;
        right: 0;
        width: 70px;
      }
      &>.market{
        width: 500px;
      }
      &>.modalText{
        position: absolute;
        top: 35%;
        left: 50%;
        transform: translate(-50%,-50%);
        text-align: center;
        font-size: 30px;
        &>span{
          color: #000;
        }
      }
      &>.price-content{
        text-align: center;
        position: absolute;
        top: 60%;
        left: 50%;
        transform: translate(-50%,-50%);
        &>div:nth-child(1){
          font-size: 40px;
        }
      }
    }
  }
  .qrcode{
    position: absolute;
    top:  200px;
    left: 150px;
    width: 210px;
  }
  .modalText{
    margin-top: 300px;
    &>span{
      font-size: 30px;
    }
    &>a{
      display: inline-block;
      width: 250px;
      height: 80px;
      background-color: rgb(63, 177, 64);
      color: #fff;
      text-align: center;
      line-height: 80px;
      margin-top: 20px;
      border-radius: 40px;
      font-size: 30px;
    }
  }
</style>


