
<style lang="less" scoped>
  header {
    background-image: url('https://image.dapengjiaoyu.com/2018/08/jl1v9cjd.png');
    background-repeat: no-repeat;
    background-size: cover;
    height: 300px;
    width: 100%;
    position: relative;
  }
  header>div {
    display: inline-block;
  }
  header>div:nth-child(1) {
    height: 140px;
    width: 140px;
    border-radius: 50%;
    border: 3px solid #fff;
    margin-left: 30px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    overflow: hidden;
  }
  header>div:nth-child(2).show_name {
    position: absolute;
    left: 23%;
    top: 50%;
    transform: translateX(-50%);
    transform: translateY(-50%);
  }
  header>div>img {
    width: 100%;
    height: 100%;
    vertical-align: middle;
  }
  header>.direction {
    width: 400px;
    height: 100%;
    position: absolute;
    left: 35%;
    top: 70%;
    transform: translateX(-50%);
    transform: translateY(-50%);
  }
  header>.direction>.direction_bg_img {
    background-image: url('../../../static/images/dl_jt.png');
    background-repeat: no-repeat;
    background-size: cover;
    margin-top: 60px;
    width: 230px;
  }
  header>.direction>.direction_bg_img>button {
    vertical-align: middle;
    border: .0625rem solid #fff;
    outline: none;
    background-color: transparent;
    color: #fff;
    border-radius: 10px;
    width: 150px;
    height: 60px;
    margin-left: 35px;
  }
  .avater_img {
    border-radius: 50%;
  }
  /* 中部操作列表部分 START */
  .mine_middle {
    background-color: #eee;
    width: 100%;
  }
  .middles {
    height: 100%;
    margin: 20px 0;
    width: 100%;
  }
  .middle_item {
    background-color: #fff;
    width: 100%;
    height: 90px;
    font-size: 30px;
  }
  .middle_item>a {
    display: block;
    height: 90px;
    line-height: 90px;
    width: 100%;
  }
  .middle_item>a>span {
    margin-left: 20px;
    vertical-align: middle;
  }
  .middle_item>a>img:nth-child(1) {
    width: 50px;
    vertical-align: middle;
    margin: auto 0 auto 20px;
  }
  .derection_item {
    float: right;
    padding: 20px 20px 0 0;
    width: 20px;
  }
  /* 中部操作列表部分 END */
  #loginModal {
    background-color: rgba(0, 0, 0, .4);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 99;
    text-align: center;
    margin: 0 auto;
    -webkit-backdrop-filter: blur(10px);
    overflow: hidden;
    padding-top: 1.875rem;
    height: 100%;
    height: 100vh;
  }
  #loginModal>div {
    width: 100%;
    height: 100%;
    position: relative;
    margin: 0 auto;
    overflow: hidden;
    margin-top: 5%;
    padding-top: .3125rem;
    height: 100vh;
  }
  #loginModal>div>h3 {
    margin-top: 6.25rem;
  }
  #loginModal>div>img:nth-child(1) {
    position: absolute;
    top: 0;
    left: 10%;
    width: 80%;
    height: 80%;
    z-index: -1;
    overflow: hidden;
  }
  .login_username {
    margin: .9375rem 0 .9375rem 0;
  }
  .close_img {
    width: 2.5rem;
    position: absolute;
    top: 7.3%;
    right: 10%;
  }
  .login_username>img,
  .login_password>img {
    width: 1.875rem;
    height: 2.1875rem;
    vertical-align: middle;
  }
  .login_username>span>input,
  .login_password>span>input {
    height: 1.875rem;
    outline: none;
    text-indent: 1em;
    border: 1px solid #aaa;
    border-radius: 0;
    margin-left: .3125rem;
    vertical-align: middle;
    border-color: transparent transparent #aaa transparent;
  }
  .login_unremember {
    margin: .9375rem 0 .9375rem 0;
  }
  .login_unremember>span:nth-child(1) {
    margin-right: 3.125rem;
  }
  .login_unremember>span:nth-child(2) {
    margin-left: 3.125rem;
  }
  .login_unremember>span:nth-child(2)>a {
    color: #44b336;
  }
  .login_button>button {
    border: none;
    outline: none;
    border-radius: .3125rem;
    background-color: #44b336;
    color: #fff;
    padding: .625rem 1.875rem;
  }
  .fade-enter {
    opacity: 0;
  }
  .fade-enter-active {
    transition: opacity .3s;
  }
  .fade-leave {
    opacity: 1;
  }
  .fade-leave-active {
    opacity: 0;
    transition: opacity .3s;
  }
  .show_name>span {
    color: #fff;
    font-size: 40px;
    display: block;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    &:nth-child(2){
      margin-left: 50px;
      font-size: 25px;
    }
  }
  .show_name>span:nth-child(1) {
    &>img {
      vertical-align: middle;
      width: 130px;
    }
    &>span:nth-child(1) {
      vertical-align: middle;
      margin-left: 50px;
      font-size: 40px;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      display: inline-block;
      max-width: 210px;
    }
  }
  .show_name {
    position: relative;
    width: 60%;
    &>img {
      margin: 0 !important;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 40px;
      height: 50px;
    }
  }
  .user-insigntrue{
    width: 330px;
  }
</style>

<template>
  <div id="app" class="globalPadding">
    
    <transition-group enter-active-class="zoomIn" tag="div" leave-active-class="zoomOut">
      <login-component v-show="loginModal" v-on:showDialogEvent="showDialogEvent" key="loginComponent"></login-component>
      <suggest v-if="showSuggest" class="animated" v-on:closeMarketModal="closeMarketModal" key="closeMarket"></suggest>
    </transition-group>
    <header :id="!LOGIN_USER_OBJECT">
      <div class="header_ava"><img @click="enterAvatar(LOGIN_USER_OBJECT.user)" :src="LOGIN_USER_OBJECT && LOGIN_USER_OBJECT.user.avatar ? LOGIN_USER_OBJECT.user.avatar : require('../../assets/img/moren.png')" alt="头像" class="avater_img"></div>
      <div class="direction" v-if="!LOGIN_USER_OBJECT">
        <div class="direction_bg_img">
          <button @click.stop="login" class="login_btn">登录</button>
        </div>
      </div>
      <div class="show_name" style="vertical-align:middle" v-if="LOGIN_USER_OBJECT" @click.stop="__change_user_info(LOGIN_USER_OBJECT && LOGIN_USER_OBJECT.user.userId)">
        <span>
          <span>{{LOGIN_USER_OBJECT && LOGIN_USER_OBJECT.user.nickname}}</span>
        <img :src="bage">
        </span>
        <span class="user-insigntrue">{{LOGIN_USER_OBJECT && LOGIN_USER_OBJECT.user.introduction}}</span>
        <img src="../../assets/img/je3sq1n6.png" alt="查看详情">
      </div>
    </header>
    <section class="mine_middle">
      <!-- <div class="middles">
        <div class="middle_item" @click="enterQuiky()">
          <a>
            <img src="../../../static/images/per_bg (5).png" alt="快捷支付">
            <span>快捷支付</span>
            <img class="derection_item" :src="direction" alt="跳转箭头">
          </a>
        </div>
      </div> -->
      <div class="middles">
        <!-- <div class="middle_item" @click="__enterMyNews(user && user.user.userId)">
          <a>
            <img src="../../../static/images/per_bg (9).png" alt="我的消息">
            <span>我的消息</span>
            <img class="derection_item" :src="direction" alt="跳转箭头">
          </a>
        </div> -->
        <div class="middle_item" @click="myWork" >
          <a>
            <img src="../../../static/images/per_bg (4).png" alt="我的课程">
            <span>我的课程</span>
            <img class="derection_item" :src="direction" alt="跳转箭头">
          </a>
        </div>
        <!-- <div class="middle_item" @click="workReview" >
          <a>
            <img src="../../../static/images/per_bg (11).png" alt="作业点评">
            <span>作业点评</span>
            <img class="derection_item" :src="direction" alt="跳转箭头">
          </a>
        </div> -->
        <!-- <div class="middle_item"  @click="enterMyWork">
          <a>
            <img src="../../../static/images/per_bg (11).png" alt="我的作业">
            <span>我的作业</span>
            <img class="derection_item" :src="direction" alt="跳转箭头">
          </a>
        </div> -->
        <!-- <div class="middle_item" @click="enterMyWorkCircle(user && user.user.userId)">
          <a>
            <img src="../../../static/images/per_bg (11).png" alt="我的作品">
            <span>我的作品</span>
            <img class="derection_item" :src="direction" alt="跳转箭头">
          </a>
        </div> -->
      </div>
      <div class="middles">
        <div class="middle_item" @click="myFriend">
          <a>
            <img src="../../../static/images/per_bg (8).png" alt="我的好友">
            <span>我的好友 </span>
            <img class="derection_item" :src="direction" alt="跳转箭头">
          </a>
        </div>
        <!-- <div class="middle_item" @click="__enterMyIntegral(user && user.user.userId)">
          <a>
            <img src="../../../static/images/per_bg (3).png" alt="我的积分">
            <span>我的积分</span>
            <img class="derection_item" :src="direction" alt="跳转箭头">
          </a>
        </div> -->
      </div>
      <div class="middles">
        <div class="middle_item" @click="enterAgreement">
          <a>
            <img src="../../assets/img/xy_zc.png" alt="协议与政策">
            <span>协议与政策</span>
            <img class="derection_item" :src="direction" alt="跳转箭头">
          </a>
        </div>
        <div class="middle_item" @click="showComplaints">
          <a>
            <img src="../../../static/images/ts.png" alt="建议与投诉">
            <span>建议与投诉</span>
            <img class="derection_item" :src="direction" alt="跳转箭头">
          </a>
        </div>
        <div class="middle_item" @click.stop="__enterSettings">
          <a>
            <img src="../../../static/images/per_bg (6).png" alt="设置">
            <span>设置</span>
            <img class="derection_item" :src="direction" alt="跳转箭头">
          </a>
        </div>
      </div>
    </section>
    <homeFooter></homeFooter>
  </div>
</template>
<script>
  import loginComponent from '@/components/login'
  import homeFooter from '@/components/homeFooter'
  import suggest from '@/components/suggest'
  import $ from 'jquery'
  import selectAccount from '@/components/selectAccount'
  import { createNamespacedHelpers } from 'vuex'
  const { mapMutations, mapActions, mapState} = createNamespacedHelpers('userModule')
  export default {
    data() {
      return {
        loginModal: false,
        bage: '',
        params: {
          phone: '',
          password: '',
          type: 'mobile'
        },
        // 关闭modal
        closeModal: 0,
        // 箭头
        direction: require('../../assets/img/je3suk2f.png'),
        showSuggest: 0,
        selectAccountStatus: 0,
        // mobileNum: null
      }
    },
    mounted() {
      
      this.$nextTick(() => {
        if(this.LOGIN_USER_OBJECT && this.LOGIN_USER_OBJECT.user.isVip === 'Y'){
          this.bage = require('../../assets/img/VIP.png')
        }else if(this.LOGIN_USER_OBJECT && this.LOGIN_USER_OBJECT.user.isTrialCourse === 'Y'){
          this.bage = require('../../assets/img/shixuexueyuan.png')
        }else if(this.LOGIN_USER_OBJECT && this.LOGIN_USER_OBJECT.user.isVip !== 'Y' && this.LOGIN_USER_OBJECT.user.isTrialCourse !== 'Y'){
          this.bage = require('../../assets/img/zhucexueyuan.png')
        }
      })
    },
    methods: {
      enterAgreement(){
        this.$router.push('/agreeMent')
      },
      selectAccount(data){
        this.selectAccountStatus = data
      },
      // 显示投诉建议
      showComplaints(){
        this.showSuggest = 1;
      },
      // 关闭投诉建议
      closeMarketModal(){
        this.showSuggest = 0;
      },
      enterMyWorkCircle(id) {
        if (id == 'false') {
          this.loginModal = true
          return;
        }
        this.$router.push({
          name: 'myWorkCircleCenter'
        });
      },
      showDialogEvent(data) {
        this.loginModal = data;
      },
      login() {
        this.loginModal = true;
      },
      __change_user_info(user_id) {
        if (user_id) {
          this.$router.push({
            name: 'baseInfo',
            params: {
              id: user_id
            }
          });
        }
      },
      enterAvatar(user) {
        this.$router.push({
          name: 'perWorkCircle',
          params: {
            id: user.userId
          }
        });
      },
      enterQuiky() {
        if(!this.$store.state.user) return
          this.$router.push({
            name: 'pay'
          });
      },
      __enterMyNews(userId) {
        if (!this.LOGIN_USER_OBJECT) {
          this.loginModal = true
          return;
        }
        this.$router.push({
          name: 'myNews'
        })
      },
      myWork() {
        if (!this.LOGIN_USER_OBJECT) {
          this.loginModal = true;
          return;
        }
        this.$router.push({
          name: 'opended'
        });
      },
      // workReview() {
      //   this.$router.push({
      //     name: 'homeReview'
      //   });
      // },
      __enterSettings() {
        this.$router.push({
          name: 'settings'
        });
      },
      closeLoginModal() {
        this.loginModal = false;
        this.params.phone = null;
        this.params.password = null;
      },
      enterMyWork() {
        if (!this.LOGIN_USER_OBJECT) {
          this.loginModal = true;
          return;
        }

        this.$router.push({
          name: 'workComment'
        });
      },
      myFriend() {
        if (!this.LOGIN_USER_OBJECT) {
          this.loginModal = true;
          return;
        }

        this.$router.push({
          name: 'myFriend',
          params: {
            id: this.LOGIN_USER_OBJECT.user.userId
          }
        });
      },
      __enterMyIntegral(userId) {
        if (!this.LOGIN_USER_OBJECT) {
          this.loginModal = true;
          return
        }
        this.$router.push({
          name: 'intertalTask'
        })
      },
    },
    components: {
      homeFooter,
      loginComponent,
      suggest,
      selectAccount,
    },
    computed: mapState([
      'LOGIN_USER_OBJECT',
    ]),
    watch: {
      LOGIN_USER_OBJECT(state){
        if(state.user.isVip === 'Y'){
          this.bage = require('../../assets/img/VIP.png')
        }else if(state.user.isTrialCourse === 'Y'){
          this.bage = require('../../assets/img/shixuexueyuan.png')
        }else{
          this.bage = require('../../assets/img/zhucexueyuan.png')
        }
        if(state){
          this.loginModal = 0
        }
      },
    }
  }
</script>


